const bubbleset = require('../bubbleset');
const events = require('../events');
const select = require('../select');

module.exports = function (event) {
    const code = event.charCode || event.keyCode;

    switch (code) {
    case 8: // Backspace
        events.backSpace(event);
        break;

    case 9: // Tab
        events.tab(event);
        break;

    case 37: // Left
        events.arrowLeft(event);
        break;

    // ÑÐ´Ð²Ð¸Ð³Ð°ÐµÐ¼ ÐºÑƒÑ€ÑÐ¾Ñ€ Ð² Ð½Ð°Ñ‡Ð°Ð»Ð¾ ÑÐ¿Ð¸ÑÐºÐ°
    case 38: // Top
        events.arrowTop(event);
        break;

    case 39: // Right
        events.arrowRight(event);
        break;

    // ÑÐ´Ð²Ð¸Ð³Ð°ÐµÐ¼ ÐºÑƒÑ€ÑÐ¾Ñ€ Ð² ÐºÐ¾Ð½ÐµÑ† ÑÐ¿Ð¸ÑÐºÐ°
    case 40: // Bottom
        events.arrowBottom(event);
        break;

    case 65: // a
        if (event.metaKey) {
            if (!events.selectAll(event)) {
                const nodeSet = bubbleset.closestNodeSet(event.currentTarget);

                if (!nodeSet) {
                    return;
                }

                select.all(nodeSet);
            }
        }
        break;
    }
};



